var awardsData=[[0,["Semi-Finalist in the BDO New Venture Competition"],["BDO and Lazaridis School of Business and Economics"],["The BDO New Venture Competition is a project that all first-year BU 121, a core business course for BBA, participate in.<br>It requires students to create a business venture idea that fits in the current business environment and a business plan related thereto.<br><br>Out of 283 competing teams, 48 teams were chosen as semi-finalists."]],[1,["Semi-Finalist in the 2020 Changemaker Innovation Challenge"],["Schlegel Centre for Entrepreneurship & Social Innovation and Lazaridis School of Business and Economics"],["The Changemaker Innovation Challenge challenges students to think differently about social and environmental change.<br>It requires students to explore and research a social or environmental issue as well as all the factors associated therewith.<br><br>Out of 250 competing teams, 30 teams were chosen as semi-finalists."]],[2,["1st Place in the Lazaridis Live Case Competition"],["HP Canada and Lazaridis School of Business and Economics"],["The Lazaridis Live Case Competition is a project that all first-year BU 111, a core business course for BBA, students participate in.<br>It presents students with a real-world problem that a company is facing in the business environment and allows students to show their research, writing, critical-thinking, problem-solving, and presentation skills through the competition.<br><br>In 2019, over 1500 students competed in the Lazaridis Live Case Competition."]]];export default awardsData;